@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css);
body{margin:0px;padding:0px}.game{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-family:Lato}.game__board{box-sizing:border-box;height:400px;background-color:#3d7fba;display:grid;grid-template-columns:0.8fr 0.8fr 0.8fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px;pointer-events:auto;margin:10px auto}.game__board__square{background-color:#ffffff;color:#000000;padding:10px;font-size:20px;cursor:pointer;width:100px}.game__title{font-size:50px;color:white;width:100%;margin:0px auto 20px;background:#3d7fba;text-align:center;padding:10px}.game__reset{width:100px;margin:10px auto;background:#3d7fba;color:white;height:50px;font-size:16px}.game__turn{font-size:30px;color:black;margin:10px auto 20px}

body {

    box-sizing: border-box;
    font-family: Lato;
    margin: 0px;
    padding: 0px;

}

a {
    text-decoration: none;
    color: black;
}

.autocomplete {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.autocomplete__results {
    width: 800px;
    margin: 0 auto;
}

.autocomplete__header {
    margin: 0px;
    background-color: #3d7fba;
    /* width: 100vw; */
    height: 100px;
    font-size: 40px;
    text-align: center;
    padding: 30px;
    box-sizing: border-box;
    color:#ffffff;


}


.autocomplete__input {

    height: 50px;
    width: 800px;
    border: 1px solid black;
    text-decoration: none;
    font-size: 18px;


}

.autocomplete__result {
    border: 1px solid #000000;
    width: 800px;
    margin: 0 auto;
    padding: 0px 0px 0px;
    padding: 1;
}

.autocomplete__result__output {
    box-sizing: border-box;
    /* margin: 0px auto; */
    padding: 15px;
    /* width: 800px; */
    height: 50px;
    /* border: 1px solid #800000; */
    background-color: #ffffff;
    text-align: center;
    font-size: 18px;
}

.autocomplete__result__output:hover {

    background-color:#65abe9;

}

.autocomplete__search {
    position: relative;
    margin: 0 auto;
}

.autocomplete__search input {
    text-indent: 30px;
}

.autocomplete__search .fa-search {
    position: absolute;
    top: 20px;
    left: 10px;
    font-size: 15px;
}

.projects{background-color:black;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}

