body {

    box-sizing: border-box;
    font-family: Lato;
    margin: 0px;
    padding: 0px;

}

a {
    text-decoration: none;
    color: black;
}

.autocomplete {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.autocomplete__results {
    width: 800px;
    margin: 0 auto;
}

.autocomplete__header {
    margin: 0px;
    background-color: #3d7fba;
    /* width: 100vw; */
    height: 100px;
    font-size: 40px;
    text-align: center;
    padding: 30px;
    box-sizing: border-box;
    color:#ffffff;


}


.autocomplete__input {

    height: 50px;
    width: 800px;
    border: 1px solid black;
    text-decoration: none;
    font-size: 18px;


}

.autocomplete__result {
    border: 1px solid #000000;
    width: 800px;
    margin: 0 auto;
    padding: 0px 0px 0px;
    padding: 1;
}

.autocomplete__result__output {
    box-sizing: border-box;
    /* margin: 0px auto; */
    padding: 15px;
    /* width: 800px; */
    height: 50px;
    /* border: 1px solid #800000; */
    background-color: #ffffff;
    text-align: center;
    font-size: 18px;
}

.autocomplete__result__output:hover {

    background-color:#65abe9;

}

@import url("//maxcdn.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css");

.autocomplete__search {
    position: relative;
    margin: 0 auto;
}

.autocomplete__search input {
    text-indent: 30px;
}

.autocomplete__search .fa-search {
    position: absolute;
    top: 20px;
    left: 10px;
    font-size: 15px;
}
